.introPage {
  width: 100%;
  max-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
}

.backgroundImage {
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -1;
}

.greeting {
  font-size: 24px;
}

.nav {
  display: -ms-flexbox;
  display: flex;
}

.navButton {
  border: 1px solid #dddddd;
  margin: 5px;
  margin-top: 15px;
  padding: 15px;
  padding-left: 20px;
  padding-right: 20px;

  -webkit-transition: all 0.2s;

  -o-transition: all 0.2s;

  transition: all 0.2s;

  height: 20px;
  width: 75px;
  text-align: center;

  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Standard syntax */
}

.navButton:hover {
  margin-top: 10px;
  background: #dddddd;
  color: #222222; 
  cursor: pointer;
}

.canvas {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -1;
}


